  @media (min-width: 800px) {
    .site-header {
      position: fixed;
      background-color: rgba(47, 85, 114, 0.3); }
      .site-header--dark {
        background-color: rgba(23, 51, 72, 0.85); } }
  @media (min-width: 800px) {
    .site-header__btn-container {
      float: right; } }
  .site-header__logo {
    position: absolute;
    top: 0;
    left: 50%;
    transform-origin: 50% 0%;
    transform: translateX(-50%) scale(0.8);
    transition: transform .3s ease-out;
    background-color: #2f5572;
    padding: 25px 36px; }
    @media (min-width: 800px) {
      .site-header__logo {
        left: auto;
        transform: translateX(0); }
        .site-header--dark .site-header__logo {
          transform: scale(0.57); } }
  .site-header__logo__graphic {
    text-indent: -9999px; }
  .site-header__menu-icon {
    width: 20px;
    height: 19px;
    position: absolute;
    z-index: 10;
    top: 10px;
    right: 10px; }
    .site-header__menu-icon::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 3px;
      background: #FFF;
      transform-origin: 0 0;
      transition: transform .3s ease-out; }
    .site-header__menu-icon__middle {
      position: absolute;
      top: 8px;
      left: 0;
      width: 20px;
      height: 3px;
      background: #FFF;
      transition: all .3s ease-out;
      transform-origin: 0 50%; }
    .site-header__menu-icon::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 20px;
      height: 3px;
      background: #FFF;
      transform-origin: 0 100%;
      transition: transform .3s ease-out; }
    @media (min-width: 800px) {
      .site-header__menu-icon {
        display: none; } }
  .site-header__menu-icon--close-x::before {
    transform: rotate(45deg) scaleX(1.25); }
  .site-header__menu-icon--close-x .site-header__menu-icon__middle {
    transform: scaleX(0);
    opacity: 0; }
  .site-header__menu-icon--close-x::after {
    transform: rotate(-45deg) scaleX(1.25) translateY(1px); }
  .site-header__menu-content {
    opacity: 0;
    transform: scale(1.2);
    transtion: all .3s ease-out;
    position: relative;
    z-index: -10;
    padding-top: 90px;
    text-align: center; }
    @media (min-width: 800px) {
      .site-header__menu-content {
        opacity: 1;
        z-index: 1;
        padding-top: 0;
        transform: scale(1); } }
    .site-header__menu-content--is-visible {
      transform: scale(1);
      opacity: 1;
      z-index: 1; }

