  visibility: hidden;
  transform: scale(1.2);
  transition: all .3s ease-out; }
  .modal--is-visible {
    opacity: 1;
    visibility: visible;
    transform: scale(1); }
  .modal__inner {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%); }
  .modal__description {
    text-align: center;
    font-size: 1.3rem;
