.large-hero {
	border-bottom: 10px solid $mainBlue;
	position: relative;

	&__image{
		display: block;
	}

	&__text-content {
		padding-top: 88px;
		position: absolute;
		top: 50%;
		left: 0px;
		width: 100%;
		transform: translateY(-50%);
		text-align: center;

		@include atMedium(){
			padding-top: 60px;
		}
	}

	&__title {
	font-weight: 300;
	color: $mainBlue;
	font-size: 2.4rem;
	margin: 0;

		@include atSmall(){
			font-size: 4.8rem;
		} 

	}

	&__subtitle {
	font-weight: 300;
	color: $mainBlue;
	font-size: 1.5rem;
	margin: 0;

		@include atSmall(){
			font-size: 2.9rem;
		}
	}

	&__description {
		color: #FFF;
		font-size: 1.1rem; 
		font-weight: 100;
		text-shadow: 2px 2px 0 rgba(0,0,0, .1);
		max-width: 30rem;
		margin-left: auto;
		margin-right: auto;

		@include atSmall(){
			font-size: 1.875rem;
		}
	}


}
